import React from "react"
import { RpJsonDocument } from "@rp/react/rpjson/types"
import { bigDataTheme } from "@rp/react/theme"
import { EllipsisTooltip } from "@rp/react/web/common/composite-components"
import { DocumentDate } from "@rp/react/web/common/document-viewer"
import { useComponentWidth } from "@rp/react/web/common/hooks"
import { FileIcon } from "@rp/react/web/icons"
import { Box, Typography } from "@rp/react/web/mui/core"
import { CustomDocumentHeaderProps } from "./DocumentViewer.types"

type DocumentViewerHeaderPaneVariantProps = {
  rpjson: RpJsonDocument
  fileExtension: string | undefined
  language: string
  customDocumentHeaderProps: CustomDocumentHeaderProps
}

function DocumentViewerHeaderPaneVariant({
  rpjson,
  fileExtension,
  language,
  customDocumentHeaderProps,
}: DocumentViewerHeaderPaneVariantProps) {
  const timestamp = rpjson.document?.timestamps_utc.analytics
  const { setComponentRef, width } = useComponentWidth()

  return (
    <Box ref={setComponentRef} position="relative">
      <Box
        p={2}
        sx={{
          backgroundColor: theme => theme.palette.elevation.dp01,
          borderBottom: theme => `1px solid ${theme.palette.grey.A700}`,
          borderTopRightRadius: "16px",
          borderTopLeftRadius: "16px",
          position: "fixed",
          top: "71px",
          width: width - 1,
          zIndex: 10,
        }}
      >
        <Box
          sx={{
            flex: 1,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            overflow: "hidden",
            textOverflow: "ellipsis",
            whiteSpace: "nowrap",
          }}
        >
          <Box mr={0.5} width="24px" height="24px">
            <FileIcon extension={fileExtension} />
          </Box>

          <EllipsisTooltip
            tooltipText={rpjson.document.metadata.provider_filename || ""}
            typographyProps={{ fontSize: 18, fontWeight: 700, flex: 1 }}
          >
            {rpjson.document.metadata.provider_filename}
          </EllipsisTooltip>

          <Box display="flex" pl={1} gap={0.5}>
            {...customDocumentHeaderProps.customButtons}
          </Box>
        </Box>

        <Box
          sx={{
            fontSize: 12,
            color: theme => theme.palette.text.secondary,
            display: "flex",
            alignItems: "center",
          }}
        >
          <Typography sx={{ pr: 1, fontSize: 12 }}>My Files</Typography>

          {language && (
            <>
              <Typography sx={{ pr: 1 }}>·</Typography>
              <Typography sx={{ pr: 1, fontSize: 12 }}>{language}</Typography>
            </>
          )}

          {timestamp && (
            <>
              <Typography sx={{ pr: 1 }}>·</Typography>
              <DocumentDate timestamp={timestamp} color={bigDataTheme.palette.text.secondary} />
            </>
          )}
        </Box>
      </Box>
    </Box>
  )
}

export { DocumentViewerHeaderPaneVariant }
